@use './colours.scss' as *;
@use './fonts.scss' as *;

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: $font-Barlow;
  // background: $LGrayish_Blue;
  background: blue;
  color: $Dark_Blue;
}
